'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { RefreshCcw } from 'lucide-react'; // Icon refresh

// UI Components
import { Button } from '@/components/ui/button';

// Komponen Anak
import RiwayatFilter from '@/components/karyawan/riwayat-transaksi/RiwayatFilter';
import RiwayatTable from '@/components/karyawan/riwayat-transaksi/RiwayatTable';
import RiwayatStrukDialog from '@/components/karyawan/riwayat-transaksi/RiwayatStrukDialog';

export default function HistoryPage() {
    const [transaksis, setTransaksis] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);
    const [searchQuery, setSearchQuery] = useState('');
    
    // State Modal
    const [selectedStruk, setSelectedStruk] = useState<any | null>(null);
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    
    // State Loading Aksi (Update Status)
    const [isProcessing, setIsProcessing] = useState(false);

    // 1. Fetch Data
    const fetchHistory = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/transaksiRestos');
            // Handle format response (jika pakai resource collection atau array biasa)
            const data = response.data.data ? response.data.data : response.data;
            
            // Sort dari yang terbaru
            const sortedData = Array.isArray(data) 
                ? data.sort((a: any, b: any) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime()) 
                : [];

            setTransaksis(sortedData);
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat riwayat transaksi.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { 
        fetchHistory(); 
    }, []);

    // 2. Filter Logic
    const filteredData = transaksis.filter(item => 
        item.kode_transaksi?.toLowerCase().includes(searchQuery.toLowerCase()) ||
        (item.nama_pelanggan && item.nama_pelanggan.toLowerCase().includes(searchQuery.toLowerCase()))
    );

    const handleViewDetail = (transaksi: any) => {
        setSelectedStruk(transaksi);
        setIsDialogOpen(true);
    };

    // 3. Handler Update Status (Logic Bisnis Utama)
    // Fungsi ini akan dipanggil dari dalam Dialog (Child Component)
    const handleUpdateStatus = async (id: number, newStatus: string) => {
        setIsProcessing(true);
        try {
            // Panggil API Backend
            await axios.patch(`/api/transaksiRestos/${id}/status`, { status: newStatus });
            
            toast.success(`Status berhasil diubah menjadi ${newStatus}`);
            
            setIsDialogOpen(false); // Tutup modal
            fetchHistory();         // Refresh data tabel agar status terbaru muncul
            
        } catch (error: any) {
            console.error(error);
            toast.error(error.response?.data?.message || "Gagal mengubah status.");
        } finally {
            setIsProcessing(false);
        }
    };

    return (
        <div className="space-y-6 pb-20 p-6 md:p-8 max-w-7xl mx-auto">
            
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Riwayat Transaksi</h1>
                    <p className="text-gray-500 text-sm">Daftar semua transaksi penjualan kasir.</p>
                </div>
                <Button variant="outline" size="sm" onClick={fetchHistory} disabled={loading}>
                    <RefreshCcw className={`w-4 h-4 mr-2 ${loading ? 'animate-spin' : ''}`} />
                    Refresh Data
                </Button>
            </div>
            
            <RiwayatFilter 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
            />

            <RiwayatTable 
                loading={loading}
                transaksis={filteredData}
                onViewDetail={handleViewDetail}
            />

            {/* Pass fungsi update dan state processing ke Dialog */}
            <RiwayatStrukDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                data={selectedStruk}
                onUpdateStatus={handleUpdateStatus} // <-- Props Baru
                isProcessing={isProcessing}         // <-- Props Baru
            />

            {/* CSS Khusus Print Struk */}
            <style jsx global>{`
                @media print {
                    /* Sembunyikan elemen layout utama */
                    body * { visibility: hidden; }
                    nav, aside, header, footer { display: none !important; }
                    
                    /* Tampilkan hanya area struk */
                    #struk-area, #struk-area * { visibility: visible; }
                    
                    #struk-area {
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 100%;
                        margin: 0;
                        padding: 0;
                    }
                    
                    @page { margin: 0; size: auto; }
                }
            `}</style>
        </div>
    );
}